.hero {

    color: #fff;
    height: 100vh;
    width: 100%;
    position: relative;
}

.hero:before {
    content: '';
    background: url('./landing_page/mainbg.jpg') no-repeat center center/cover;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -2;
}

.hero .content {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 1100px;
    margin: auto;
    padding: 1rem;
}

.hero .content p:first-child {
    font-weight: 700;
    font-size: 4rem;
}

.hero .content p:nth-child(2) {
    font-size: 2rem;
    font-weight: 700;
}

.hero .content p:nth-child(3) {
    font-size: 1rem;
    font-weight: 700;
    font-style: italic;
}

.link__styl {
    display: flex;
    flex-direction: row;
    
    padding-top: 1rem;
}
.link__styl:hover { 
    color:#fff;
    text-shadow:0 0 10px #fff,
                0 0 20px rgb(226, 247, 112),
                0 0 40px rgb(255, 251, 13);
}


.hero .button {
    margin: 1rem 0;
    width: 300px;
    background-color: var(--secondary-color);
    border: 1px solid var(--secondary-color);
    color: #333;
}


.hero .button:hover {
    background-color: var(--secondary-dark);
    border: 1px solid var(--secondary-dark);
}

